import { cssProps } from '@oracle/oraclejet-cssprops';
import { classNames } from '../utils/UNSAFE_classNames';
import { MessageVariant } from './Message.types';

const messageSummaryStyles = {
  // TODO: Reevaluate once the TEXT component is available (JET-46891)
  base: cssProps`${{
    flex: '1',
    overflowWrap: 'anywhere'
  }}`,

  banner: cssProps`${{
    fontSize: 'var(--oj-c-PRIVATE-DO-NOT-USE-message-banner-summary-text-size)',
    fontWeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-message-banner-summary-text-weight)',
    color: 'var(--oj-c-PRIVATE-DO-NOT-USE-message-banner-summary-text-color)'
  }}`,

  inline: cssProps`${{
    fontSize: 'var(--oj-c-PRIVATE-DO-NOT-USE-user-assistance-inline-font-size)',
    fontWeight: '400',
    color: 'var(--oj-c-PRIVATE-DO-NOT-USE-user-assistance-inline-text-color)',
    lineHeight: 1.333
  }}`,

  toast: cssProps`${{
    fontSize: 'var(--oj-c-PRIVATE-DO-NOT-USE-message-toast-summary-text-size)',
    color: 'var(--oj-c-PRIVATE-DO-NOT-USE-message-toast-summary-text-color)'
  }}`
};

/**
 * Props for the Summary component
 */
type Props = {
  /**
   * The summary text
   */
  text?: string;

  /**
   * Style variant
   */
  variant?: MessageVariant;
};

/**
 * Summary Component for rendering the summary text of the Message
 */
function MessageSummary({ text, variant = 'banner' }: Props) {
  // If detail content is rendered, then wrap it in a div with specified style classes
  // TODO: JET-48715 - remove marker classes
  // Right now, oj-message-banner component uses this for unit tests and has no alternate way for
  // testing. This will need to be removed as we work on oj-c-message-banner.
  const markerClassForTests = 'oj-c-messagebanner-summary';
  const classes = classNames([
    variant === 'banner' && markerClassForTests,
    messageSummaryStyles.base,
    messageSummaryStyles[variant]
  ]);
  return (
    <div role="heading" class={classes}>
      {text}
    </div>
  );
}

export { MessageSummary };
